import { HTMLAttributes, useEffect, useState } from 'preact/compat';
import { Container, toLanguageTag } from '@adobe-commerce/elsie/lib';
import { Price, PriceRange } from '@/pdp/components';
import { events } from '@adobe-commerce/event-bus';
import { ProductModel } from '@/pdp/data/models';
import { config, getFetchedProductData } from '@/pdp/api';

export interface ProductPriceProps extends HTMLAttributes<HTMLDivElement> {
  scope?: string;
}

export const ProductPrice: Container<
  ProductPriceProps,
  ProductModel | null
> = ({ initialData = null, scope, children, ...props }) => {
  const { defaultLocale = 'en-US' } = config.getConfig();

  const [data, setData] = useState<ProductModel | null>(initialData);

  const [locale, setLocale] = useState<string>(defaultLocale);

  useEffect(() => {
    const dataEvent = events.on('pdp/data', setData, { scope });
    const localeEvent = events.on('locale', setLocale);

    return () => {
      dataEvent?.off();
      localeEvent?.off();
    };
  }, [scope]);

  if (!data || !data.prices) return null;

  return (
    <Price
      {...props}
      specialPrice={
        data.prices?.visible ? (
          <PriceRange {...data.prices?.final} locale={toLanguageTag(locale)} />
        ) : undefined
      }
      price={
        data.prices?.visible &&
        data.prices?.regular &&
        data.prices?.final.amount !== data.prices?.regular.amount ? (
          <PriceRange
            {...data.prices?.regular}
            locale={toLanguageTag(locale)}
          />
        ) : undefined
      }
    />
  );
};

ProductPrice.getInitialData = getFetchedProductData;
